import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

import data from './assets/data';

const store = new Vuex.Store({
  state: {
    isOpen: false,
    index: 0,
    post: {}
  },
  mutations: {
    openModal(state, index) {
      state.isOpen = true;
      state.index = index;
      state.post = data[index];
    },

    prevPost: function(state) {
      if(state.index == 0) {
        state.index = data.length - 1;
      } else {
        state.index--;
      }

      state.post = data[state.index];
    },

    nextPost: function(state) {
      if(state.index == data.length - 1) {
        state.index = 0;
      } else {
        state.index++;
      }

      state.post = data[state.index];
    },

    close(state) {
      state.isOpen = false;
    }
  }
})

export default store;