<template lang="pug">

  transition(name='slide-left')
    .modal(v-if="isOpen")
      div
        div {{post.title}}
        div {{post.description}}

      button(@click="closeModal") CLOSE

      button(@click="prevPost") BACK
      button(@click="nextPost") NEXT

</template>

<script>
  import store from '../store.js';

  export default {
    data: function() {
      return {
        post: {},
        isOpen: false
      }
    },
    watch: {
      listenForStoreChanges() { }
    },
    methods: {
      closeModal: function() {
        store.commit('close');
      },
      prevPost: function() {
        store.commit('prevPost');
      },
      nextPost: function() {
        store.commit('nextPost');
      }
    },
    computed: {
      listenForStoreChanges() {
        this.post = store.state.post;
        this.isOpen = store.state.isOpen;
        return null;
      }
    }
  }
</script>

<style lang="scss" scoped>
  .modal {
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    width: 100vw;
    height: 100vh;
    background: #fff;
  }

  .slide-left-enter-active, .slide-left-leave-active {
    transition: transform .3s ease;
  }

  .slide-left-enter, .slide-left-leave-to {
    transform: translateX(100%);
  }
</style>